<template>
	<div>
		<p style="cursor: pointer; color: #18aeff;" @click="showDrawer"> 详情 </p>
		<a-drawer :title="movie?movie.name:''" :width="900" :visible="visible" :body-style="{ paddingBottom: '80px' }" @close="onClose">
			<a-form-model :model="movie" :label-col="labelCol" :wrapper-col="wrapperCol">
				<div style="display: flex; justify-content: space-around;">
					<div>
						<a-form-model-item label="导演">
							<a-input v-model="movie.directors" class="movie_inputField" :disabled="!modify" />
						</a-form-model-item>
						<a-form-model-item label="编剧">
							<a-input v-model="movie.writers" class="movie_inputField" :disabled="!modify" />
						</a-form-model-item>
						<a-form-model-item label="演员">
							<a-input v-model="movie.stars" class="movie_inputField" :disabled="!modify" />
						</a-form-model-item>
						<a-form-model-item label="播放时长">
							<a-input v-model="movie.playTime" class="movie_inputField" :disabled="!modify" />
						</a-form-model-item>
						<a-form-model-item label="发行时间">
							<a-date-picker v-model="movie.releaseTime" type="date" placeholder="Pick a date" class="movie_inputField" :disabled="!modify"
							 @change="onChange" />
						</a-form-model-item>
					</div>
					<a-form-model-item>
						<a-upload name="file" list-type="picture-card" class="poster-uploader-movieDetail" :show-upload-list="false" :action="this.$store.state.requestPath + 'uploadMoviePoster'"
						 :before-upload="beforeUpload" @change="handleChange">
							<img v-if="imageUrl" :src="imageUrl" style="width: 200px; height: 300px;" />
							<div v-else>
								<a-icon :type="picloading ? 'loading' : 'plus'" />
								<div class="ant-upload-text">
									上传
								</div>
							</div>
						</a-upload>
					</a-form-model-item>
				</div>
				<div style="margin: -50px 0 0 -20px;">
					<a-form-model-item label="类别">
						<a-checkbox-group v-model="record.genre" style="margin:10px 0 0 20px; width: 80%;" :disabled="!modify">
							<a-row>
								<a-col v-for="(item, index) in genreList" :span="5">
									<a-checkbox :value="item" :name="item" style="vertical-align:middle;">
										{{item}}
									</a-checkbox>
								</a-col>
							</a-row>
						</a-checkbox-group>
					</a-form-model-item>
					<a-form-model-item label="介绍">
						<a-input v-model="movie.intro" type="textarea" style="margin-left: 10px;" :disabled="!modify" />
					</a-form-model-item>
					<a-form-model-item label="修改开关">
						<a-switch v-model="modify" style="margin-left: 10px;" />
					</a-form-model-item>
					<a-form-model-item :wrapper-col="{ span: 14, offset: 4 }">
						<a-button type="primary" @click="onSubmit" :disabled="!modify">
							确定
						</a-button>
						<a-button style="margin-left: 30px;" @click="onClose">
							返回
						</a-button>
					</a-form-model-item>
				</div>
			</a-form-model>
		</a-drawer>
	</div>
</template>
<script>
	import {
		mapMutations,
		mapState
	} from 'vuex'

	function getBase64(img, callback) {
		const reader = new FileReader();
		reader.addEventListener('load', () => callback(reader.result));
		reader.readAsDataURL(img);
	};
	export default {
		computed: {
			...mapState(['userInfo', 'requestPath','picRequestPath'])
		},
		data() {
			return {
				labelCol: {
					span: 4
				},
				wrapperCol: {
					span: 14
				},
				genreList: ['动作', '冒险', '动画', '儿童', '喜剧', '犯罪', '纪录', '戏剧',
					'幻想', '黑色', '恐怖', '音乐', '神秘', '浪漫', '科幻', '惊悚', '战争', '西部', '其他'
				],
				picloading: false,
				modify: false,
				visible: false,
				imageUrl: '',
				latestImageUrl: null,
				movie: null,
			};
		},
		props: ['record'],
		async created() {
			let {
				data
			} = await this.$get(this.$store.state.requestPath + 'admin/movie/' + this.record.mid);
			this.movie = data;
			this.imageUrl = this.picRequestPath + 'poster/' + data.pic;
			this.latestImageUrl = this.imageUrl;
		},
		watch: {
			record: async function(obj) {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'admin/movie/' + obj.mid);
				this.movie = data;
				this.imageUrl = this.picRequestPath + 'poster/' + data.pic;
				this.latestImageUrl = this.imageUrl;
			}
		},
		methods: {
			showDrawer() {
				this.visible = true;
			},
			onChange(date, dateString) {
				this.movie.releaseTime = dateString;
			},
			onClose() {
				this.visible = false;
				this.modify = false;
				this.movie.pic = this.latestImageUrl;
				this.imageUrl = this.latestImageUrl;
			},
			async onSubmit() {
				this.movie.genre = this.record.genre;
				let {
					data
				} = await this.axios.post(this.requestPath + 'modifyMovie', this.movie);
				if (data.code == 0) {
					this.record.releaseTime = this.movie.releaseTime
					this.record.directors = this.movie.directors
					this.$message.success('修改成功');
				} else {
					this.$message.error('修改失败');
				}
				this.onClose();
			},
			handleChange(info) {
				if (info.file.status === 'uploading') {
					this.picloading = true;
					return;
				}
				if (info.file.status === 'done') {
					// 从前端获取上传的 img-url
					getBase64(info.file.originFileObj, imageUrl => {
						this.imageUrl = imageUrl;
						this.picloading = false;
					});
					this.movie.pic = info.file.response.data.src;
				}
			},
			beforeUpload(file) {
				const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
				if (!isJpgOrPng) {
					this.$message.error('You can only upload JPG file!');
				}
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
					this.$message.error('Image must smaller than 2MB!');
				}
				return isJpgOrPng && isLt2M;
			},
		},
	};
</script>
<style lang='scss'>
	.movie_inputField {
		margin-left: 20px;
		width: 320px;
	}

	.poster-uploader-movieDetail>.ant-upload {
		width: 200px;
		height: 300px;
	}

	.ant-upload-select-picture-card i {
		font-size: 16;
		color: #999;
	}

	.ant-upload-select-picture-card .ant-upload-text {
		margin-top: 8px;
		color: #666;
	}
</style>
